.main-notes{
    position: absolute;
    width: 1000px;
    height: 100vh;
    top: 0;
    left: 435px; 
}
.add-note-container{
    position: absolute;
    width: 1000px;
    height: 90vh;
    top: 0;
}
.notes-grp-card{
    position: absolute;
    width: 1000px;
    height: 98px;
    display: flex;
    background: hsla(0, 0%, 91%, 1);

}
.arrow{
    display: none;
    position: absolute;
    width: 15.575019836425781px;
    height: 11.15000057220459px;
    top: 27px;
    left: 29.5750732421875px;
    color: hsla(0, 0%, 36%, 1);
}
.notes-grp-btn{
    position: relative;
    width: 60px;
    height: 60px;
    top: 20px;
    left: 35px;
    border: none;
    border-radius: 60px;
}
.notes-grp-Name{
    position: relative;
    width: 159px;
    height: 28px;
    top: 15px;
    left: 55px;
    font-family: 'Roboto';
    font-size: 23px;
    font-weight: 600;
    line-height: 28px;
    letter-spacing: 0.02em;
    text-align: left;
}
.note-card{
    position: absolute;
    width: 1000px;
    height: 420px;
    top: 98px;
    border-radius: 0px 0px 0px 15px;
    background-color: hsla(35, 62%, 92%, 1);
    overflow: scroll;
}
ul{
    list-style-type: none;
}
.addedNotes{
    display: flex;
    width: 900px;
    margin: 10px;
}
.notesTime{
    width: 85px;
    height: 17px;
    margin: 5px;
    font-family: Roboto;
    font-size: 16px;
    font-weight: 600;
    line-height: 18px;
    letter-spacing: 0.02em;
    text-align: left;
    color: black;
}
.notesDate{
    width: 120px;
    height: 18px;
    margin: 5px;
    font-family: Roboto;
    font-size: 16px;
    font-weight: 600;
    line-height: 18px;
    letter-spacing: 0.02em;
    text-align: left;
}
.notesText{
    width: 750px;
    margin-top: 8px;
    font-family: 'Roboto';
    font-size: 18px;
    font-weight: 500;
    overflow-wrap: break-word;
}
.enter-notes{
    position: absolute;
    width: 1000px;
    height: 255px;
    top: 500px;
    background: hsla(0, 0%, 91%, 1);
    position: fixed;
    border-bottom-left-radius: 20px;
}
.input-card{
    position: absolute;
    width: 930px;
    height: 211px;
    top: 22px;
    left: 32px;
    border-radius: 9px;
    border: 1px;
    background-color: hsla(0, 0%, 100%, 1);
    border: 1px solid hsla(0, 0%, 80%, 1);
    font-size: 15px;
}
.enter-btn{
    position: absolute;
    height: 30px;
    width: 30px;
    border: none;
    top: 190px ;
    left: 920px;
    background-image: url("./images/enter.png");
    background-color: white;
}

@media screen and (max-width:480px) and (max-height:800px) {

    .add-note-container{
        display: none;
    }
    .notes-grp-card{
        max-width: 480px;
        height: 90px;
        left: 0;
        top: 0;
    }
    .arrow{
        display: block;
    }
    .note-card{
        width: 480px;
        height: 90vh;
        left: 0;

    }
    .enter-notes{
        width: 480px;
        height: 220px;
        top: 630px;
        left: 0;
        background: #E8E8E8;
    }
    .input-card{
        width: 400px;
        height: 150px;
        background: #FFFFFF;
        border: none;
        border-radius: 9px;
        padding: 20px;
        font-size: 20px;
        left:20px;
        top:15px;
    }
    .enter-btn{
        position: absolute;
        height: 30px;
        width: 30px;
        border: none;
        top:155px ;
        left: 400px;
        background-image: url("./images/enter.png");
        background-color: none;
    
    }
    .addedNotes{
        gap: 5px;
        
    }
    .notesTime{
        width: 80px;
        height: 20px;
        left: 5px;
        top: 10px;
        font-family: 'Roboto';
        font-weight: 500;
        font-size: 15.6345px;
        color: #353535;
    }
    .notesDate{
        width: 80px;
        height: 20px;
        left: 5px;
        top: 10px;
        font-family: 'Roboto';
        font-weight: 500;
        font-size: 15.6345px;
        color: #353535;
    }
    .notesText{
        width: 430px;
        top: 5px;
        font-family: 'Roboto';
        font-weight: 350;
        font-size: 17px;
        color: #000000;
        text-align: justify;
        padding: 3px;
    }
    ul{
        list-style-type: none;
        padding: 0px 0px 0px 10px;
    }


    
}









































